@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.auth-form__input {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  border: solid 1px #04aaff;
  margin-bottom: 10px;
  font-size: 12px;
  color: #fff;
}

.auth-form__submit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.auth-form__error {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.auth-switch {
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

.nweet-factory-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.nweet-factory-form__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.nweet-factory-form__input-text {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.nweet-factory-form__submit {
  position: absolute;
  right: 0;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.nweet-factory-form__submit:hover {
  cursor: pointer;
}

.nweet {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 10px;
  border-radius: 10px;
  position: relative;
  display: flex;
  align-items: center;
  color: rgba(0, 0, 0, 0.8); 
}

.nweet.mode-edit {
  flex-direction: column;
}

.nweet:hover .nweet-me {
  display: none;
}

.nweet:hover .nweet-actions {
  display: block;
}

.nweet img, svg {
  border-radius: 50%;
}

.nweet h4 {
  font-size: 14px;
  margin-left: 10px;
}

.nweet-form--submit {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.nweet-me {
  display: block;
  position: absolute;
  right: 10px;
  border-radius: 10px;
  padding: 5px 10px;
  color: #fff;
}

.nweet-actions {
  display: none;
  position: absolute;
  right: 10px;
}

.nweet-actions > span {
  cursor: pointer;  
}

.nweet-actions > span:first-of-type {
  margin-right: 10px;
}

.navigation__list {
  display: flex; 
  justify-content: center; 
  margin-top: 50px;   
}

.navigation__list > li > .navigation__list--link {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navigation__list > li:first-of-type {
  margin-right: 20px;
}

.navigation__list > li > .navigation__list--link > img {
  border-radius: 50%;
}

.navigation__list > li > .navigation__list--link > span {
  margin-top: 10px;
}
.router {
  max-width: 890px; 
  width: 100%; 
  margin: 0 auto; 
  margin-top: 80px; 
  display: flex; 
  justify-content: center;
}
.app__init {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 50vh;
}
.file-module {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.file-module-label {
  cursor: pointer;   
}  

.file-module-label > span {
  margin-right: 10px;
  font-size: 12px;
}

.file-module-input {
  opacity: 0;
  height: 0;
}

.file-module__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;   
  align-items: center;
  margin-top: 10px;
}

.file-module__attachment-img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
  
}

.file-module__attachment-clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;  
  margin-top: 10px;
}

.file-module__attachment-clear > svg {
  vertical-align: bottom;
}

.file-module__attachment-clear > span {
  margin-right: 10px;
  font-size: 12px;
}
.auth {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.auth__social {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.auth__social > button {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

.profile-edit-button {
  text-align: center;
  margin-bottom: 10px;
  border: 1px solid #04aaff;
  border-radius: 20px;
  padding: 10px;
}

.profile-edit-button:hover {
  cursor: pointer;
}

.profile-form {
  padding-bottom: 30px;
  margin-top: 10px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.profile-form__submit {
  margin-top: 10px;
}

.profile__my-nweets {
  border-top: 1px solid #04aaff;
  margin-top: 10px;
  padding-top: 10px;
}

.profile__my-nweets > h1 {
  text-align: center;
  color: #04aaff
}

.profile__my-nweets-contents {
  margin-top: 30px;
}

.profile__my-nweets-loading {
  text-align: center;
}

.profile-form__cancel {
  margin-top: 50px;
}

.home__nweets {
  margin-top: 30px;
}

* {
  box-sizing: border-box;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

footer {
  text-align: center;
  padding-top: 20px;
  margin-top: 20px;
}

form {
  width: 100%;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

input::-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

input::placeholder {
  color: white;
  opacity: 0.9;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.form-input {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.form-btn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  cursor: pointer;
}

.cancel-btn {
  cursor: pointer;
  background-color: tomato;
}

.brand-font-color {
  color: #04aaff;
}

.brand-bg-color {
  background-color: #04aaff;
}
